.theme-blue .back-bar,.theme-green .back-bar{filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffeeeeee', endColorstr='#ffdddddd', GradientType=0)}
.slider-container{margin:13px auto 0;display:inline-block;width:300px}
.slider-container .back-bar{height:8px;position:relative}
.slider-container .back-bar .selected-bar{position:absolute;height:100%}
.slider-container .back-bar .pointer{position:absolute;width:10px;height:10px;background-color:red;cursor:col-resize;opacity:1;z-index:2}
.slider-container .back-bar .pointer.last-active{z-index:3}
.slider-container .back-bar .pointer-label{position:absolute;top:-25px;font-size:8px;white-space:nowrap;line-height:1}
.slider-container .back-bar .focused{z-index:10}
.slider-container .clickable-dummy{cursor:pointer;position:absolute;width:100%;height:100%;z-index:1}
.slider-container .scale{top:5px;position:relative}
.slider-container .scale span{position:absolute;height:5px;border-left:1px solid #999;font-size:0}
.slider-container .scale ins{font-size:12px;text-decoration:none;position:absolute;left:0;top:5px;color:#999;line-height:1}
.slider-container.slider-readonly .clickable-dummy,.slider-container.slider-readonly .pointer{cursor:auto}
.theme-green .back-bar{height:5px;border-radius:2px;background-color:#e7e7e7;background-image:-webkit-gradient(linear,0 0,0 100%,from(#eee),to(#ddd));background-image:-webkit-linear-gradient(top,#eee,#ddd);background-image:linear-gradient(to bottom,#eee,#ddd);background-repeat:repeat-x}
.theme-green .back-bar .selected-bar{border-radius:2px}
.theme-green .back-bar .pointer{width:20px;height:20px;top:-10px;-moz-box-sizing:border-box;box-sizing:border-box;border-radius:10px;border:1px solid #4ed2c1;background:#4ed2c1}
.theme-green .back-bar .pointer-label{color:#4ed2c1}
.theme-green .back-bar .focused{color:#333}
.theme-green .scale span{border-left:1px solid #e5e5e5}
.theme-green .scale ins{color:#999}
.theme-blue .back-bar{height:5px;border-radius:2px;background-color:#e7e7e7;background-image:-webkit-gradient(linear,0 0,0 100%,from(#eee),to(#ddd));background-image:-webkit-linear-gradient(top,#eee,#ddd);background-image:linear-gradient(to bottom,#eee,#ddd);background-repeat:repeat-x}
.theme-blue .back-bar .selected-bar{border-radius:2px}
.theme-blue .back-bar .pointer{width:14px;height:14px;top:-5px;-moz-box-sizing:border-box;box-sizing:border-box;border-radius:10px;border:1px solid #4ed2c1;background:#4ed2c1}
.theme-blue .back-bar .pointer-label{color:#4ed2c1}
.theme-blue .back-bar .focused{color:#333}
.theme-blue .scale span{border-left:1px solid #e5e5e5}
.theme-blue .scale ins{color:#999}

